.skill {
    width: 250px;
    /*background-color: burlywood;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    /*padding: 20px;*/
    /*box-sizing: border-box;*/
    margin: 0;
    padding: 30px 30px 30px 30px;
    border: 1px solid #565656;
    border-radius: 20px;
    line-height: 1.7;
    transition: .3s;

}

.skill:hover {
    /* Стили кнопки при наведении */
    border: 1px solid #28e98c;

}

.icon {
    height: 60px;
    width: 60px;
    /*background-color: #c7ecce;*/
    color: #28e98c;
}

.description {
    font-size: 14px;
    color: #999999;
    line-height: 1.7;
    /*font-weight: 400;*/
}

.title {
    font-size: 20px;
    font-weight: 300;
    color: white;
}

