.distantWorkBlock{
    height: 50vh;
    /*background-color: #000000;*/

}

.distantWorkContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

h3 {
    color: #ffffff;
}

.button {
    background: #28e98c;
    color: #000000;

    text-align: center;
    padding: 11px 58px 10px 58px;

    align-items: center;
    border-radius: 30px;
    justify-content: center;
    text-transform: uppercase;
   font-size: 16px;
    transition: .3s;
    cursor: pointer;
    border: 2px solid #28e98c;
}

.button:hover {
    /* Стили кнопки при наведении */
    background-color: transparent;
    color: #28e98c;
}