.imageAndButton {
    /*border: 1px solid;*/
    position: relative;
    /*    background-color: #f59898;*/
    background-image: url("https://miro.medium.com/v2/resize:fit:0/0*KIKnUvzdIkp5zcDJ");

    background-size: cover;
    background-position: center;
    height: 200px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    /*border: 1px solid #565656;*/
    border-radius: 20px;

}


.imageAndButton::before {
    position: absolute;
    content: "";
    background-color: black;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.4;

    /*content: '';*/
    /*background-color: black;*/
    /*transition: .6s;*/
    /*opacity: 0.4;*/
}

